<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="4" :sm="5"
        ><el-input
          size="small"
          v-model="userInput"
          placeholder="用户名"
        ></el-input
      ></el-col>
      <el-col :span="4" :sm="5">
        <el-select v-model="selectValue" placeholder="请选择" size="small">
          <el-option
            v-for="item in selectOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4" :sm="6">
        <el-button-group>
          <el-button
            @click="shuaXin"
            size="small"
            type="primary"
            icon="el-icon-refresh"
          ></el-button>
          <el-button @click="chaXun" size="small" type="primary"
            >查询</el-button
          >
        </el-button-group>
      </el-col>
      <el-col :span="10"></el-col>
    </el-row>
    <div id="Table">
      <el-table :data="tableData" style="width: 100%" :key="Math.random()">
        <!-- :key="Math.random()" 可以实现表格自动刷新 -->

        <el-table-column label="用户id" width="120">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column label="姓名" width="150">
          <template slot-scope="scope">
            <span>{{ scope.row.name }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="80">
          <template slot-scope="scope">
            <el-tag v-show="!scope.row.state" type="success">正常</el-tag>
            <el-tag v-show="scope.row.state" type="danger">异常</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="邮箱" width="220">
          <template slot-scope="scope">
            <span>{{ scope.row.email }}</span>
          </template>
        </el-table-column>
        <el-table-column label="联系方式" width="180">
          <template slot-scope="scope">
            <span>{{ scope.row.phone }}</span>
          </template>
        </el-table-column>
        <el-table-column label="地址" width="190">
          <template slot-scope="scope">
            <span>{{ scope.row.address }}</span>
          </template>
        </el-table-column>
        <el-table-column label="日期" width="180">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span> {{ scope.row.date }}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <el-dialog title="编辑信息" :visible.sync="dialogOpen">
        <div>
          <el-form :model="editUser" label-width="80px" ref="editUser">
            <el-form-item label="名称" prop="name">
              <el-input v-model="editUser.name"></el-input>
            </el-form-item>
            <el-form-item label="邮箱">
              <el-input v-model="editUser.email"></el-input>
            </el-form-item>
            <el-form-item label="地址">
              <el-input v-model="editUser.address"></el-input>
            </el-form-item>
            <el-form-item label="日期">
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="editUser.date"
                value-format="yyyy-MM-dd"
                style="width: 100%"
              >
              </el-date-picker>
            </el-form-item>
            <el-form-item label="联系方式">
              <el-input v-model="editUser.phone"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogOpen = false">取 消</el-button>
          <el-button type="primary" @click="editTrue">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "User",
  data() {
    return {
      dialogOpen: false,
      editUser: "",
      tableData: [],
      DataAJ: [
        {
          id: "0001",
          name: "王小虎",
          email: "1235425@qq.com",
          address: "杭州市江干区",
          date: "2021-11-25",
          phone: "13256464654",
          state: false,
        },
        {
          id: "0002",
          name: "王2虎",
          email: "1235425@qq.com",
          address: "上海市",
          date: "2021-11-25",
          phone: "13256464654",
          state: true,
        },
        {
          id: "0003",
          name: "王33虎",
          email: "123123123125@qq.com",
          address: "杭州市江干区",
          date: "2021-11-25",
          phone: "13256464654",
          state: false,
        },
        {
          id: "0004",
          name: "王4虎",
          email: "1235425@qq.com",
          address: "温州市",
          date: "2021-11-25",
          phone: "13256464654",
          state: false,
        },
        {
          id: "0005",
          name: "王5虎",
          email: "1235425@qq.com",
          address: "温州市",
          date: "2021-11-25",
          phone: "13256464654",
          state: false,
        },
        {
          id: "0006",
          name: "王6虎",
          email: "1235425@qq.com",
          address: "温州市",
          date: "2021-11-25",
          phone: "13256464654",
          state: false,
        },
        {
          id: "0007",
          name: "王7虎",
          email: "1235425@qq.com",
          address: "温州市",
          date: "2021-11-25",
          phone: "13256464654",
          state: false,
        },
        {
          id: "0008",
          name: "王8虎",
          email: "1235425@qq.com",
          address: "温州市",
          date: "2021-11-25",
          phone: "13256464654",
          state: false,
        },
      ],
      search: "",
      selectOptions: [
        {
          value: false,
          label: "正常",
        },
        {
          value: true,
          label: "异常",
        },
        {
          value: "all",
          label: "全部",
        },
      ],
      selectValue: "all",
      userInput: "",
    };
  },
  methods: {
    handleEdit(index, row) {
      // this.editUser=this.tableData[index]  //有错
      this.dialogOpen = true;
      this.editUser = Object.assign({}, row); //引用值的深拷贝问题,可以用Object.prototype.assign
      this.editUser.EditIndex = index;
      // console.log(this.editUser , "修改值");
    },
    editTrue() {
      // 修改结束

      this.tableData[this.editUser.EditIndex] = this.editUser;

      this.dialogOpen = false;
      setTimeout(() => {
        this.$message({
          message: "修改成功！",
          type: "success",
        });
      }, 100);
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.tableData.splice(index, 1);
      // 删除元素
    },
    shuaXin() {
      location.reload();
    },
    chaXun() {
      // 查找用户名  并显示到表格
      console.log(this.userInput);
      this.tableData = "";
      console.log("chaxun");
    },
  },
  watch: {
    selectValue() {
      this.tableData = this.DataAJ;
      console.log(this.selectValue);
      let b = this.tableData.filter((data) => {
        return data.state === this.selectValue || this.selectValue === "all";
      });
      //filter() 方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素。
      this.tableData = b;
    },
  },
  mounted() {
    this.tableData = this.DataAJ;
  },
};
</script>

<style scoped>
#Table {
  margin-top: 15px;
}
</style>